import React,{Component} from 'react'

class User extends Component{
    constructor(props){
        super(props)
        this.state={popup:false}
    }
    change(){
        this.setState({popup:true})
    }
    render(){
        return(
            <div>{
                (this.state.popup)
                ?<div>
                    {this.props.children}
                <div id='outer'>
                    <div id='inner'>
                        <button>View Orders</button>
                        <p>LogOut</p>
                    </div>
                    </div>
                </div>
                :(<div><div id='button'><p onClick={this.change}>User</p></div>
                {this.props.children}
                </div>)
            }
            <style jsx>
            {`p{
                font-family:sans-serif;
                font-size:1em;
            }
            #outer{
                height:100%;
                width:100%;
                background-color:#f1f1f1;
                position:absolute;
            }
            #inner{
                text-align:center
                padding:1em;
                border:1px solid black;
                left: 25%;
                right: 25%;
                top: 25%;
                bottom: 25%;
                height: 30%;
                margin: auto;
                background: white;
            }
           #button{
               margin-top:8em;
              position:absolute;
              margin-left:110em;
              font-size:1em;
              cursor:pointer;
            }
            `}
            </style>
            </div>
        )
    }
}

export default User


